
$color-primary: #55c57a;
$color-primary-light: #7ed56f;
$color-primary-dark: #28b485;

/*No need to use normalize - this simple reset will work*/
*,
*::after,
*::before{
  margin: 0;
  padding: 0;
  /*so we have set the box-sizing property to inherit here - because it will force all elements to inherit this style - we set the actual box-sizing value on the body*/
  box-sizing: inherit;
}

html{
  /*this is a percentage that equates to 10px - however we use % as a default font size because we want the font size to scale effectively according to (if) the user changes his/her web browser*/
  font-size: 62.5%;
}

body{
  font-family: "lato", sans-serif;
  font-weight: 400;
  /* font-size: 16px; */
  line-height: 1.7;
  color: #777;
  padding: 3rem;
  box-sizing: border-box;
}

.header{
  height: 95vh;
  background-image: linear-gradient(to right bottom, rgba($color-primary-light, 0.8), rgba($color-primary-dark, 0.8)) ,url(../img/hero.jpg);
  background-size: cover;
  /*the back pos declaration makes sure that the img stays at the top of the container at all times/sizes*/
  background-position: top;
  /*research clip path */
  clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
  position: relative;
}

.header__logo-box{
  position: absolute;
  top: 4rem;
  left: 4rem;
}

.header__logo{
  height: 3.5rem;
}

.header__text-box{
  position: absolute;
  /*top and left %'s are in relation to the parent element */
  top: 40%;
  left: 50%;
  /*this will shift half of the width this element to the left - and half of the height of this element up */
  transform: translate(-50%, -50%);
  text-align: center;
}

.heading-primary{
  color: #fff;
  text-transform: uppercase;
  backface-visibility: hidden;
  margin-bottom: 6rem;
}

.heading-primary--main{
  display: block;
  font-size: 6rem;
  font-weight: 400;
  letter-spacing: 3.5rem;

  animation-name: moveInLeft;
  animation-duration: 1s;
  /*defines how the animation will proceed - how fast or how slow */
  animation-timing-function: ease-out;

  /*the animation will happen 3 times*/
  /* animation-iteration-count: 3 */

  /*so before animation starts it will delay 3s*/
  /* animation-delay: 3s; */
}

.heading-primary--sub{
  display: block;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1.75rem;
  animation: moveInRight 1s ease-out;
}

@keyframes moveInLeft {

/*start of animation - initial state*/
/*negative value pushes element from the right hand side of the screen to the left*/
  0%{
    opacity: 0;
    transform: translateX(-10rem);
  }

  /*positive value pushes element from the left hand side of the screen to the right*/
  80%{
    transform: translateX(1rem);
  }

/*end of animation - final state*/
  100%{
    opacity: 1;
    transform: translate(0);
  }

}

@keyframes moveInRight {

  0%{
    opacity: 0;
    transform: translateX(10rem);
  }

  80%{
    transform: translateX(-1rem);
  }

  100%{
    opacity: 1;
    transform: translate(0);
  }

}

@keyframes moveInBottom {

  0%{
    opacity: 0;
    transform: translateY(3rem);
  }

  100%{
    opacity: 1;
    transform: translate(0);
  }

}

/*link psuedo class*/
.btn:link,
.btn:visited{
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 4rem;
  /*just using the inline block element creates the natural browser margin and padding of elements - inline-block element are treated as if it is texted*/
  display: inline-block;
  border-radius: 10rem;
  transition: all .2s;
  position: relative;
  font-size: 1.6rem;
}

.btn:hover{
  /*we want this element to move up - so we target the y axis - because the y axis moves from top to bottom - the negative value pulls it up */
  transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0,0,0,.2);
}

.btn:active{
  transform: translateY(-1px);
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
}

.btn--white{
  background-color: #fff;
  color: #777;
}

/* with the after pseudo element we are designing the same button - again to enable the hover affect to be effective */

.btn::after{
  /*with the psuedo after element we MUST display the content/display properties*/
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  /*we want to position the element behind the button - it will be relative to the btn (NOT the header)*/
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all .4s;
}

.btn--white::after{
  background-color: #fff;
}

/*this is an after pseudo element ONLY when we have the hover state*/
.btn:hover::after{
  /*scale increases the element  - if we use scale 2, then it doubles the size*/
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.btn--animated{
  /*animation - name - duration - timing function - delay*/
  animation: moveInBottom .5s ease-out .75s;
  /*this declaration will apply the moveInBottom styles before the animation starts*/
  animation-fill-mode: backwards;

}
